import * as React from 'react';
import type { SinglePilet } from 'piral-base';
import Page from './Page'

const DemoDiv = ({ authToken, children, piral, changableValue }) => {
	
    return <div className='local-pilet-div' style={{background: "lightblue"}}>
		<p>LocalPilet</p>
		<p>AuthToken is {authToken}. This is passed from props</p>
		<p>This is fetched from piral.getdata --- foo: {piral.getData("name")}</p>
		<p>This is fetched from piral.getdata and then put to state --- changableFoo: {changableValue}</p>
        {children}
    </div>
}

// allow-unused-export -- pulled in via require()
export const demoPilet: SinglePilet = {
	name: 'demopilet',
	version: '0.0.1',
	spec: 'v2',
	dependencies: {},
	config: {},
	basePath: '',
	link: 'fakelink',
	setup(api) {

		function usePiralStoreValueHook(name){
			const [value, setValue] = React.useState("")
			React.useEffect(()=>{
				const handler = (ev) => {
					console.log("store-data listen",ev.name)
					if (ev.name === name) {
						setValue(ev.value)
					}
				};
				api.on("store-data", handler);

			},[name])
			
		
			return value
		}

		
		api.registerPage("/localpiletpage", Page);

		api.registerExtension('dashboard-splash', ({piral}) => {
			const changableFoo = usePiralStoreValueHook("changableFoo");
			return <DemoDiv authToken="token12345" piral={piral} changableValue={changableFoo}>This came through a pilet extension slot</DemoDiv>
		});
	},
};